"use client"

import React, { useEffect, useState } from "react"
import { Card } from "@/components/ui/card"
import { getAllExerciseSessions } from "@/lib/utils"

interface Session {
  exerciseType: string
  duration: number
  date: string
}

export function ExerciseHistory() {
  const [sessions, setSessions] = useState<Session[]>([])

  useEffect(() => {
    const data = getAllExerciseSessions()
    setSessions(data)
  }, [])

  const formatDuration = (seconds: number) => {
    const mins = Math.floor(seconds / 60)
    const secs = seconds % 60
    return `${mins}m ${secs}s`
  }

  if (sessions.length === 0) {
    return (
      <div className="text-center text-gray-500 py-4">
        No exercise sessions recorded yet
      </div>
    )
  }

  return (
    <div className="space-y-3 max-h-72 overflow-y-auto">
      {sessions.map((session, index) => (
        <Card key={index} className="p-4 hover:bg-gray-50 transition-colors">
          <div className="flex justify-between items-start">
            <div>
              <h3 className="font-semibold">{session.exerciseType}</h3>
              <p className="text-sm text-gray-600">
                Duration: {formatDuration(session.duration)}
              </p>
            </div>
            <time className="text-xs text-gray-500">
              {new Date(session.date).toLocaleDateString()}
            </time>
          </div>
        </Card>
      ))}
    </div>
  )
}
