"use client"

import React, { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { ExerciseTimer } from "./ExerciseTimer"
import { ExerciseHistory } from "./ExerciseHistory"
import { saveExerciseSession } from "@/lib/utils"

export function ExerciseTracker() {
  const [exerciseType, setExerciseType] = useState("")
  const [time, setTime] = useState(0)
  const [isRunning, setIsRunning] = useState(false)
  const [isPaused, setIsPaused] = useState(false)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    let interval: NodeJS.Timeout
    if (isRunning && !isPaused) {
      interval = setInterval(() => {
        setTime((prev) => prev + 1)
      }, 1000)
    }
    return () => clearInterval(interval)
  }, [isRunning, isPaused])

  const handleStart = () => {
    if (!exerciseType.trim()) {
      setError("Please enter a valid exercise type.")
      return
    }
    setIsRunning(true)
    setIsPaused(false)
    setTime(0)
    setError(null)
  }

  const handlePause = () => {
    if (!isRunning) return
    setIsPaused(true)
  }

  const handleResume = () => {
    if (!isRunning || !isPaused) return
    setIsPaused(false)
  }

  const handleStop = () => {
    if (!isRunning) return
    setIsRunning(false)
    setIsPaused(false)
    saveExerciseSession({
      exerciseType,
      duration: time,
      date: new Date().toISOString(),
    })
    setTime(0)
    setExerciseType("")
  }

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setExerciseType(e.target.value)
    if (error) setError(null)
  }

  return (
    <div className="max-w-md mx-auto p-4 space-y-6">
      <div className="text-center">
        <h1 className="text-3xl font-bold mb-2">Exercise Tracker</h1>
        <p className="text-gray-600">Track your daily workouts</p>
      </div>

      <Card className="p-6 space-y-4">
        <div className="space-y-2">
          <label className="text-sm font-medium">Exercise Type</label>
          <Input
            type="text"
            value={exerciseType}
            onChange={handleInputChange}
            placeholder="e.g., Running, Cycling, Swimming"
            className="w-full"
          />
          {error && (
            <Alert variant="destructive" className="mt-2">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}
        </div>

        <div className="py-4">
          <ExerciseTimer time={time} />
        </div>

        <div className="flex gap-2 justify-center flex-wrap">
          {!isRunning && (
            <Button onClick={handleStart} className="w-full">
              Start Exercise
            </Button>
          )}
          {isRunning && !isPaused && (
            <Button onClick={handlePause} variant="outline" className="flex-1 min-w-[120px]">
              Pause
            </Button>
          )}
          {isRunning && isPaused && (
            <Button onClick={handleResume} variant="outline" className="flex-1 min-w-[120px]">
              Resume
            </Button>
          )}
          {isRunning && (
            <Button onClick={handleStop} variant="destructive" className="flex-1 min-w-[120px]">
              Stop
            </Button>
          )}
        </div>
      </Card>

      <Card className="p-6">
        <h2 className="text-xl font-semibold mb-4">Exercise History</h2>
        <ExerciseHistory />
      </Card>
    </div>
  )
}
