import { type ClassValue, clsx } from "clsx"
import { twMerge } from "tailwind-merge"

export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs))
}

export interface Session {
  exerciseType: string
  duration: number
  date: string
}

const STORAGE_KEY = "exercise_sessions"

export function saveExerciseSession(session: Session) {
  try {
    const sessions = getAllExerciseSessions()
    sessions.push(session)
    localStorage.setItem(STORAGE_KEY, JSON.stringify(sessions))
  } catch (error) {
    console.error("Error saving session:", error)
  }
}

export function getAllExerciseSessions(): Session[] {
  try {
    if (typeof window === "undefined") return []
    const data = localStorage.getItem(STORAGE_KEY)
    return data ? (JSON.parse(data) as Session[]) : []
  } catch (error) {
    console.error("Error retrieving sessions:", error)
    return []
  }
}
